<template>
  <div>
    <top-guide></top-guide>
    <h1>{{ msg }}</h1>
    <el-button type="text" @click="open">点击打开 Message Box</el-button>
  </div>
</template>

<script>
import TopGuide from "../common/topGuide";
export default {
  components: {TopGuide},
  data () {
    return {
      msg: '首页'
    }
  },
  methods: {
    open() {
      this.$alert('这是一段内容', '标题名称', {
        confirmButtonText: '确定',
        callback: action => {
          this.$message({
            type: 'info',
            message: `action: ${ action }`
          });
        }
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



